<template>
    <div class="register">
        <h3>用户注册</h3>
        <p>
            <Input prefix="ios-contact" v-model="username" placeholder="请输入用户名" class="input" @on-blur="validateUser()"/>
            <span class="err">{{errmsg}}</span>
        </p>
        <p>
            <Input prefix="ios-lock" v-model="password" placeholder="请输入密码" class="input" @on-blur="validatePass1()"/>
            <span class="err">{{errmsg1}}</span>
        </p>
        <p>
            <Input type="password" prefix="ios-lock" v-model="passagain" placeholder="请再次输入密码" class="input" @on-blur="validatePass2()"/>
            <span class="err">{{errmsg2}}</span>
        </p>
        <p>
            <Button type="primary" @click="Register()">注册</Button>
            <Button type="primary">重置</Button>
        </p>
    </div>
