</template>

<script>
    import axios from 'axios'
    export default {
        name: "Register",
        data(){
            return {
                username: '',
                password: '',
                passagain: '',
                errmsg: '',
                errmsg1: '',
                errmsg2: ''
            }
                // formInline:{
                //     username:'',
                //     password:'',
                //     passagain:''
                // },
                // ruleInline:{
                //     username: [
                //         {required:true,message:'请输入用户名',trigger:'blur'},
                //         {type:'string',asyncValidator:(rule,value,callback)=>{
                //             axios.get('http://localhost:8080/api/notexist?username'+value)
                //                 .then((res)=>{
                //                     if (res.data)
                //                     callback()
                //                 else
                //                     callback(new Error(''))
                //                 });
                //             },
                //             message: '用户名已存在',
                //             trigger: 'blur'
                //         }
                //     ],
                //     password: [
                //         {required: true,message:'请输入密码', trigger:'blur'},
                //         {type:'string',pattern:/^\w{6,16}$/,message:'密码不符合规范',trigger:'blur'}
                //     ],
                //     passagain: [
                //         {required: true,message:'请输入密码',trigger:'blur'},
                //         {type:'string',validator:(rule,value)=>{
                //             return value==this.formInline.password;
                //             },
                //             message: '密码不一致',trigger: 'blur'
                //         }
                //
                //     ]
                //
                // }

        },
        methods:{
            validateUser(){
                let valid=true;
                if (this.username==''){
                    this.errmsg = '用户名不能为空';
                    valid=false;
                }else {
                    axios.get('http://localhost:8080/api/isexist',{params:{username:this.username}})
                        .then((keep)=>{
                            if (keep.data){
                                this.errmsg='用户名已存在'
                                valid=false;
                            }
                        })
                }
                if (valid=true)
                    this.errmsg='';
                return valid;

            },
            validatePass1(){
                let valid=true;
                if (this.password==''){
                    this.errmsg1='密码不能为空';
                    valid=false;
                }else {
                    var sw=new RegExp(/^\w{6,16}$/)
                    valid=sw.test(this.password);
                    if (!valid)
                        this.errmsg1='密码需为6-16位字符'
                }
                if (valid)
                    this.errmsg1='';
                return valid;
            },
            validatePass2(){
                let valid=true;
                if (this.passagain==''){
                    valid=false;
                    this.errmsg2='请输入密码';
                }else {
                    if (this.passagain!=this.password){
                        valid=false;
                        this.errmsg2='两次密码不一致'
                    }
                }
                if (valid)
                    this.errmsg2='';
                return valid;
            },
            // handleSubmit(name){
            //     this.$refs[name].validate((valid)=> {
            //         if (valid){
            //             this.registerUser();
            //         }
            //     })
            //
            // },
            // registerUser(){
            //     let username=this.formInline.username;
            //     let password=this.formInline.password;
            //     axios.post('http://localhost:8080/api/register',{username: username,password: password})
            //         .then((res)=>{
            //             if (res.data){
            //                 alert("注册成功,页面跳转到登录界面");
            //                 this.$router.push('/login')
            //             }else
            //                 alert('注册错误')
            //
            //         })
            //
            // },
            validate(){
                let validate1=this.validateUser();
                let validate2=this.validatePass1();
                let validate3=this.validatePass2();
                return validate1&&validate2&&validate3;
            },
            Register(){
                if (this.validate()){
                    axios.post('http://localhost:8080/api/register',
                        {username:this.username,password:this.password
                        })
                        .then((res)=>{
                            alert("注册成功，将跳转到登陆页面");
                            this.$router.push('/login');
                        })
                }
            }
        }
    }
</script>

<style scoped>
    .register{width:480px; margin:30px auto; border:1px solid #2d8cf0; padding:20px 5px 10px 30px; border-radius:5px; text-align:left;}
    .err{color:#ed4014;}
    .register .input{width:280px;}
    .register .btn{width:100px; margin-right:20px;}
    .register p,.register h3{margin-bottom:15px;}
</style>